___CSS_LOADER_EXPORT___.push([module.id, `#forecast {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  justify-items: stretch;
  text-align: center;
  gap: 25px;
}

.card {
  height: max(300px, 15dvh);
  background-color: white;
  border-radius: 10px;
  box-shadow: var(--darker-shadow);
  display: flex;
  align-items: center;
  justify-content: center;
}

.card .inner {
  height: 85%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.card .day,
.card .weather {
  color: var(--dark-clr);
  opacity: 0.7;
}

.card .day {
  font-weight: 600;
}

.card .weather {
  font-size: 0.7rem;
}

.card .icon {
  height: 75px;
  width: 75px;
  filter: brightness(0.97);
}

.temperature {
  font-size: 2rem;
}

.min-temp {
  opacity: 0.7;
}

.rain-chance-container .property-name {
  font-size: 0.6rem;
  opacity: 0.7;
}
