___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --dark-clr: hsl(203, 30%, 26%);
  --mid-clr: hsl(206, 46%, 37%);
  --lighter-clr: hsl(201, 51%, 69%);
  --lightest-clr: hsl(203, 90%, 93%);
  --gradient-top-clr: hsl(205, 80%, 48%);
  --light-orange-clr: #f6ae2d;
  --darker-shadow: 1px 2px 6px 0px rgba(0, 0, 0, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-weight: 300;
  color: var(--dark-clr);
  font-size: 1.5rem;
}

b {
  font-weight: 600;
}

body {
  width: 900px;
  height: 100dvh;
  padding: 0 15px;
  margin: 0 auto;
  background: linear-gradient(var(--lightest-clr), white);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 8fr;
}

main {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 1fr;
}

ul {
  list-style-type: none;
}
